import React, { Component } from 'react'
//记录
import Times from "@/components/Time/index";
import dataed from "@/config/tableList/";
import {Table} from "antd";
import { observer, inject } from 'mobx-react';
@inject('AllList')
@observer
// recordList
class Record extends Component {
    state={
        brr:["今日","昨日","本周","本月","最近7日","最近30日"]
    }
    render() {
        let {brr} = this.state;
        let {AllList} = this.props;
        let {recordList} = AllList;
         return (
            <div className="wrap">
                <div className="record-top">
                <Times title="审批日期" btnTitle="批量审批" brrList={brr} buttonOpen={false} buttonTitle="导出"/>
                </div>
                <div className="record-bottom">
                  <Table dataSource={recordList} columns={dataed.recordList} pagination={false}/>
                </div>
            </div>
        )
    }
}
export default Record
