import React, { Component } from 'react'
import { Button, DatePicker, Form, Select, Checkbox } from 'antd';
import "./style/style.scss";
const { RangePicker } = DatePicker;
const { Option, OptGroup } = Select;
export default class Time extends Component {
    state = {
        openTitle: "展开查询",
        ind: 0,
        open: false
    }
    render() {
        let { openTitle, ind, open } = this.state;
        let { buttonOpen, title, btnTitle, brrList } = this.props;
        return (
            <>
                <div className="box">
                    <div className="left">
                        <div className="button">
                            <Button type="primary">{btnTitle}</Button>
                        </div>
                        <ul>
                            <span>{title}：</span>
                            {
                                brrList.map((item, index) =>
                                    <li key={index} className={index === ind ? 'active' : ''} onClick={() => {
                                        this.setState({
                                            ind: index
                                        })
                                    }
                                    }
                                    >{item}
                                    </li>)
                            }
                        </ul>
                        <Form>
                            <Form.Item>
                                <RangePicker
                                    dateRender={current => {
                                        const style = {};
                                        if (current.date() === 1) {
                                            style.border = '1px solid #1890ff';
                                            style.borderRadius = '50%';
                                        }
                                        return (
                                            <div className="ant-calendar-date" style={style}>
                                                {current.date()}
                                            </div>
                                        );
                                    }}
                                />
                            </Form.Item>
                        </Form>
                    </div>
                    {
                        buttonOpen && <div className="right">
                            <Button type="primary" onClick={() => {
                                this.setState({
                                    open: !open
                                })
                                if (openTitle === "展开查询") {
                                    this.setState({
                                        openTitle: "关闭查询"
                                    })
                                } else {
                                    this.setState({
                                        openTitle: "展开查询"
                                    })
                                }
                            }}
                            >{openTitle}</Button>
                        </div>
                    }
                </div>
                <div className="all-bottom" style={{
                    display: open ? "block" : "none"
                }}
                >
                    <div className="all-left">
                        <div className="all-left-top">
                            <Form>
                                <Form.Item>
                                    <span>部门：</span>
                                    <Select defaultValue="lucy" style={{ width: 200 }}>
                                        <OptGroup label="Manager">
                                            <Option value="jack">Jack</Option>
                                            <Option value="lucy">Lucy</Option>
                                        </OptGroup>
                                    </Select>
                                </Form.Item>
                                <Form.Item>
                                    <span>职务：</span>
                                    <Select defaultValue="lucy" style={{ width: 200 }}>
                                        <OptGroup label="Manager">
                                            <Option value="jack">Jack</Option>
                                            <Option value="lucy">Lucy</Option>
                                        </OptGroup>
                                    </Select>
                                </Form.Item>
                                <Form.Item>
                                    <Checkbox>包含已停用员工</Checkbox>
                                </Form.Item>
                            </Form>
                        </div>
                        <div className="all-left-bottom">
                            <Form>
                                <Form.Item>
                                    <span>员工：</span>
                                    <Select defaultValue="lucy" style={{ width: 200 }}>
                                        <OptGroup label="Manager">
                                            <Option value="jack">Jack</Option>
                                            <Option value="lucy">Lucy</Option>
                                        </OptGroup>
                                    </Select>
                                </Form.Item>
                                <Form.Item>
                                    <span>考勤组：</span>
                                    <Select defaultValue="lucy" style={{ width: 200 }}>
                                        <OptGroup label="Manager">
                                            <Option value="jack">Jack</Option>
                                            <Option value="lucy">Lucy</Option>
                                        </OptGroup>
                                    </Select>
                                </Form.Item>
                                <Form.Item>
                                    <Checkbox>待审核</Checkbox>
                                </Form.Item>
                            </Form>
                        </div>
                    </div>
                    <div className="all-right">
                        <Button type="primary">查询</Button>
                        <Button type="primary">清除</Button>
                    </div>
                </div>
            </>
        )
    }

}
